import { Fragment, Suspense } from 'react';
import { BrowserRouter as Router, Route, Routes } from 'react-router-dom';

import { publicRoutes } from './routes';

// Import the functions you need from the SDKs you need
import { initializeApp } from 'firebase/app';
import { getAnalytics } from 'firebase/analytics';
// TODO: Add SDKs for Firebase products that you want to use
// https://firebase.google.com/docs/web/setup#available-libraries

// Your web app's Firebase configuration
// For Firebase JS SDK v7.20.0 and later, measurementId is optional
const firebaseConfig = {
    apiKey: 'AIzaSyAp6uoP6JZi-FQEgqNTODw-T3hBdJiD9Aw',
    authDomain: 'clone-a2d10.firebaseapp.com',
    projectId: 'clone-a2d10',
    storageBucket: 'clone-a2d10.appspot.com',
    messagingSenderId: '953847638853',
    appId: '1:953847638853:web:5e27094a9aeb5722a72c2c',
    measurementId: 'G-8R09RBJS65',
};

// Initialize Firebase
const app = initializeApp(firebaseConfig);
const analytics = getAnalytics(app);

const App = () => {
    return (
        <Router>
            <div className="App">
                <Routes>
                    {publicRoutes &&
                        publicRoutes.length > 0 &&
                        publicRoutes.map((route, index) => {
                            let Layout = route.layout ? route.layout : Fragment;
                            let Page = route.component;

                            return (
                                <Route
                                    key={index}
                                    path={route.path}
                                    element={
                                        <Layout>
                                            <Suspense>
                                                <Page />
                                            </Suspense>
                                        </Layout>
                                    }
                                />
                            );
                        })}
                </Routes>
            </div>
        </Router>
    );
};

export default App;
